
  .header {
    max-width: 100%;
    display: flex;
    background-color: $grey1;
    background-image: url("../../img/bg-bike-desktop.png");
    background-repeat: no-repeat;
    background-position: top -1px center;
  }

  .header__wrap {
    max-width: 100%;
    box-sizing: border-box;
    width: $desktop;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 auto;
    padding: 22px 73px 218px 73px;
  }

  .nav-button {
    display: none;
  }

  .main-nav {
    width: 100%;
  }

  .main-nav__list {
    box-sizing: border-box;
    padding: 0 24px 7px 21px;
    margin-bottom: 255px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border-bottom: 2px solid $white3;

  }

  .main-nav__link {
    font-family: "OpenSans-Light", "Arial", sans-serif;
    font-size: 16px;
    line-height: 32px;
    letter-spacing: -0.005em;
    color: $white2;
    //opacity: 0.6;
    flex-grow: 1;

    &:hover {
      border-bottom: 1px solid $white2;
    }

    &:active {
      color: $white;
      border: none;
    }
  }

  .description {
    box-sizing: border-box;
    width: 393px;
    color: $white;
  }

  .description__title {

    font-family: "OpenSans-Bold", "Arial", sans-serif;
    font-size: 46px;
    line-height: 64px;
    margin: 26px 0 25px 0;
    font-stretch: normal;
    letter-spacing: -0.005em;
  }

  .description__text {
    font-family: "OpenSans-Light", "Arial", sans-serif;
    font-size: 18px;
    line-height: 32px;
    opacity: 0.8;
    letter-spacing: -0.005em;
  }

  .form {
    box-sizing: border-box;
    width: 327px;
    color: $white;
  }

  .form__title {
    font-family: "OpenSans-SemiBold", "Arial", sans-serif;
    text-align: center;
    font-size: 17px;
    line-height: 28px;
    margin: 5px 0 10px 0;
    letter-spacing: -0.005em;
  }

  .form__input {
    box-sizing: border-box;
    width: 100%;
    font-size: 15px;
    line-height: 24px;
    background-color: $white;
    border: none;
    border-radius: 4px;
    padding: 14px 10px 9px 30px;
    margin: 10px 0;

    &::placeholder {
      color: $grey2;
      letter-spacing: 0.021em;
    }
  }

  .form__button {
    font-family: "OpenSans-SemiBold", "Arial", sans-serif;
    width: 100%;
    font-size: 15px;
    line-height: 21px;
    background-color: $aqua;
    border: none;
    border-radius: 4px;
    padding: 16px 30px 14px 30px;
    margin: 11px 0;
  }

@media (max-width: $tablet) {
  .header {
    background-image: none;
  }

  .description {
    width: 100%;
    padding: 25px 25px 77px 22px;
    background-image: url("../../img/bg-bike-desktop.png");
    background-repeat: no-repeat;
    background-size: 765px auto;
    background-position: top -52px center;
  }

  .description__title {
    font-size: 30px;
    line-height: 42px;
    text-align: center;
    margin: 85px 0 19px 5px;
    word-wrap: break-word;
  }

  .description__text {
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
    padding-left: 4px;
  }

  .header__wrap {
    padding: 0 0 218px 0;
  }

  .nav-button--close {
    display: block;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    border: none;
    background-color: $aqua;

    &::before {
      content: "";
      position: absolute;
      top: 18px;
      left: 14px;
      width: 22px;
      height: 2px;
      background-color: $black;
      box-shadow: 0 6px 0 0 $black, 0 12px 0 0 $black;
    }

    &:hover {
      background-color: $aqua2;
    }
  }

  .nav-button--open {
    display: block;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    border: none;
    background-color: transparent;

    &::before {
      content: "";
      position: absolute;
      width: 2px;
      height: 30px;
      top: 10px;
      left: 24px;
      border-radius: 2px;
      background-color: $grey6;
      transform: rotate(45deg);
      box-shadow: none;
    }

    &::after {
      content: "";
      position: absolute;
      width: 2px;
      height: 30px;
      top: 10px;
      left: 24px;
      border-radius: 2px;
      background-color: $grey6;
      transform: rotate(-45deg);
    }

    &:hover {
      background-color: $grey5;
    }

    &:active {
      background-color: $grey3;
    }
  }

  .main-nav {
    background-color: $white;
    color: black;
  }

  .main-nav--none {
    display: none;
  }

  .main-nav__list {
    box-sizing: border-box;
    padding: 0;
    margin: 100px 0 138px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 2px solid $white3;
  }

  .main-nav__item {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 45%;
      right: 30px;
      width: 5px;
      height: 5px;
      border-top: 2px solid $grey6;
      border-right: 2px solid $grey6;
      transform: rotate(45deg);
    }
  }

  .main-nav__link {
    display: block;
    background-color: $grey4;
    box-sizing: border-box;
    margin: 15px 0 15px 0;
    padding: 14px 35px 14px 16px;
    color: $grey3;
    flex-grow: 1;

    &:hover {
      border: none;
      background-color: $grey5;
      color: $white;
    }

    &:active {
      color: $white2;
      background-color: $grey3;
    }
  }

  .form-wrap {
    width: 100%;
    box-sizing: border-box;
    margin: 0 auto;
    background-color: $white;
  }

  .form {
    max-width: 321px;
    width: 100%;
    padding: 68px 0 0 0;
    margin: 0 auto;
  }

  .form__title {
    width: 100%;
    color: $black;
    font-size: 18px;
    line-height: 28px;
    margin: 5px 0 20px 0;
  }

  .form__input {
    width: 100%;
    border: 1px solid $grey7;
    padding: 12px 10px 10px 20px;
  }

  .form__button {
    width: 100%;
    padding: 16px 30px 14px 30px;
    margin: 10px 0 11px 0;
  }
}

@media (max-width: $mobile) {
  .description {
    width: 100%;
    padding: 25px 25px 70px 17px;
    background-image: url("../../img/bg-bike-mobile.png");
    background-repeat: no-repeat;
    background-size: 320px 390px;
    background-position: top center;
  }

  .description__title {
    margin: 71px 0 19px 6px;
  }

  .description__text {
    padding-left: 6px;
  }

  .form-wrap {
    padding: 0 16px;
  }

  .form {
    padding: 65px 0 0 0;
  }

  .form__title {
    margin: 5px 0 10px 0;
  }

  .form__input {
    padding: 13px 10px 9px 25px;
  }
}
