@charset "UTF-8";
@font-face {
  font-family: "OpenSans-Light";
  src: url("../fonts/OpenSans-Light.woff") format("woff"), url("../fonts/OpenSans-Light.woff2") format("woff2");
  /* Modern Browsers */
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "OpenSans-Regular";
  src: url("../fonts/OpenSans-Regular.woff") format("woff"), url("../fonts/OpenSans-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "OpenSans-SemiBold";
  src: url("../fonts/OpenSans-SemiBold.woff") format("woff"), url("../fonts/OpenSans-SemiBold.woff2") format("woff2");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "OpenSans-Bold";
  src: url("../fonts/OpenSans-Bold.woff") format("woff"), url("../fonts/OpenSans-Bold.woff2") format("woff2");
  font-weight: normal;
  font-style: normal; }

html {
  margin: 0;
  padding: 0; }
  html::-webkit-scrollbar {
    /* chrome based */
    width: 0px;
    /* ширина scrollbar'a */
    background: transparent;
    /* опционально */ }

body {
  font-family: "Open-Sans", "Arial", sans-serif;
  font-size: 16px;
  width: 100%;
  margin: 0 auto; }

.ul-global {
  list-style: none;
  margin: 0;
  padding: 0; }

.a-global {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  max-width: 100%;
  display: flex;
  background-color: #1F1F1F;
  background-image: url("../../img/bg-bike-desktop.png");
  background-repeat: no-repeat;
  background-position: top -1px center; }

.header__wrap {
  max-width: 100%;
  box-sizing: border-box;
  width: 1024px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 22px 73px 218px 73px; }

.nav-button {
  display: none; }

.main-nav {
  width: 100%; }

.main-nav__list {
  box-sizing: border-box;
  padding: 0 24px 7px 21px;
  margin-bottom: 255px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-bottom: 2px solid rgba(255, 255, 255, 0.1); }

.main-nav__link {
  font-family: "OpenSans-Light", "Arial", sans-serif;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: rgba(255, 255, 255, 0.5);
  flex-grow: 1; }
  .main-nav__link:hover {
    border-bottom: 1px solid rgba(255, 255, 255, 0.5); }
  .main-nav__link:active {
    color: #fff;
    border: none; }

.description {
  box-sizing: border-box;
  width: 393px;
  color: #fff; }

.description__title {
  font-family: "OpenSans-Bold", "Arial", sans-serif;
  font-size: 46px;
  line-height: 64px;
  margin: 26px 0 25px 0;
  font-stretch: normal;
  letter-spacing: -0.005em; }

.description__text {
  font-family: "OpenSans-Light", "Arial", sans-serif;
  font-size: 18px;
  line-height: 32px;
  opacity: 0.8;
  letter-spacing: -0.005em; }

.form {
  box-sizing: border-box;
  width: 327px;
  color: #fff; }

.form__title {
  font-family: "OpenSans-SemiBold", "Arial", sans-serif;
  text-align: center;
  font-size: 17px;
  line-height: 28px;
  margin: 5px 0 10px 0;
  letter-spacing: -0.005em; }

.form__input {
  box-sizing: border-box;
  width: 100%;
  font-size: 15px;
  line-height: 24px;
  background-color: #fff;
  border: none;
  border-radius: 4px;
  padding: 14px 10px 9px 30px;
  margin: 10px 0; }
  .form__input::placeholder {
    color: rgba(28, 28, 28, 0.5);
    letter-spacing: 0.021em; }

.form__button {
  font-family: "OpenSans-SemiBold", "Arial", sans-serif;
  width: 100%;
  font-size: 15px;
  line-height: 21px;
  background-color: #0AD9C6;
  border: none;
  border-radius: 4px;
  padding: 16px 30px 14px 30px;
  margin: 11px 0; }

@media (max-width: 768px) {
  .header {
    background-image: none; }
  .description {
    width: 100%;
    padding: 25px 25px 77px 22px;
    background-image: url("../../img/bg-bike-desktop.png");
    background-repeat: no-repeat;
    background-size: 765px auto;
    background-position: top -52px center; }
  .description__title {
    font-size: 30px;
    line-height: 42px;
    text-align: center;
    margin: 85px 0 19px 5px;
    word-wrap: break-word; }
  .description__text {
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
    padding-left: 4px; }
  .header__wrap {
    padding: 0 0 218px 0; }
  .nav-button--close {
    display: block;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    border: none;
    background-color: #0AD9C6; }
    .nav-button--close::before {
      content: "";
      position: absolute;
      top: 18px;
      left: 14px;
      width: 22px;
      height: 2px;
      background-color: #000;
      box-shadow: 0 6px 0 0 #000, 0 12px 0 0 #000; }
    .nav-button--close:hover {
      background-color: #71FFFF; }
  .nav-button--open {
    display: block;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    border: none;
    background-color: transparent; }
    .nav-button--open::before {
      content: "";
      position: absolute;
      width: 2px;
      height: 30px;
      top: 10px;
      left: 24px;
      border-radius: 2px;
      background-color: #91929B;
      transform: rotate(45deg);
      box-shadow: none; }
    .nav-button--open::after {
      content: "";
      position: absolute;
      width: 2px;
      height: 30px;
      top: 10px;
      left: 24px;
      border-radius: 2px;
      background-color: #91929B;
      transform: rotate(-45deg); }
    .nav-button--open:hover {
      background-color: #222222; }
    .nav-button--open:active {
      background-color: #48494D; }
  .main-nav {
    background-color: #fff;
    color: black; }
  .main-nav--none {
    display: none; }
  .main-nav__list {
    box-sizing: border-box;
    padding: 0;
    margin: 100px 0 138px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 2px solid rgba(255, 255, 255, 0.1); }
  .main-nav__item {
    position: relative; }
    .main-nav__item::before {
      content: "";
      position: absolute;
      top: 45%;
      right: 30px;
      width: 5px;
      height: 5px;
      border-top: 2px solid #91929B;
      border-right: 2px solid #91929B;
      transform: rotate(45deg); }
  .main-nav__link {
    display: block;
    background-color: #F6F6F6;
    box-sizing: border-box;
    margin: 15px 0 15px 0;
    padding: 14px 35px 14px 16px;
    color: #48494D;
    flex-grow: 1; }
    .main-nav__link:hover {
      border: none;
      background-color: #222222;
      color: #fff; }
    .main-nav__link:active {
      color: rgba(255, 255, 255, 0.5);
      background-color: #48494D; }
  .form-wrap {
    width: 100%;
    box-sizing: border-box;
    margin: 0 auto;
    background-color: #fff; }
  .form {
    max-width: 321px;
    width: 100%;
    padding: 68px 0 0 0;
    margin: 0 auto; }
  .form__title {
    width: 100%;
    color: #000;
    font-size: 18px;
    line-height: 28px;
    margin: 5px 0 20px 0; }
  .form__input {
    width: 100%;
    border: 1px solid #C9CCD4;
    padding: 12px 10px 10px 20px; }
  .form__button {
    width: 100%;
    padding: 16px 30px 14px 30px;
    margin: 10px 0 11px 0; } }

@media (max-width: 320px) {
  .description {
    width: 100%;
    padding: 25px 25px 70px 17px;
    background-image: url("../../img/bg-bike-mobile.png");
    background-repeat: no-repeat;
    background-size: 320px 390px;
    background-position: top center; }
  .description__title {
    margin: 71px 0 19px 6px; }
  .description__text {
    padding-left: 6px; }
  .form-wrap {
    padding: 0 16px; }
  .form {
    padding: 65px 0 0 0; }
  .form__title {
    margin: 5px 0 10px 0; }
  .form__input {
    padding: 13px 10px 9px 25px; } }
