
html {
  margin: 0;
  padding: 0;

  &::-webkit-scrollbar { /* chrome based */
    width: 0px;  /* ширина scrollbar'a */
    background: transparent;  /* опционально */
  }
}



body {
  font-family: "Open-Sans", "Arial", sans-serif;
  font-size: 16px;
  width: 100%;
  margin: 0 auto;
}

.ul-global {
  list-style: none;
  margin: 0;
  padding: 0;
}

.a-global {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
